import { EditOutlined, DeleteOutlined, ImageOutlined } from "@mui/icons-material";
import { Box, Divider, Typography, InputBase, useTheme, Button, IconButton } from "@mui/material";
import FlexBetween from "../../components/FlexBetween";
import Dropzone from "react-dropzone";
import UserImage from "../../components/userImage";
import WidgetWrapper from "../../components/widgetWrapper";
import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { setPosts } from "../../state";
import { useNavigate } from "react-router-dom";

const MyPostWidget = ({ picturePath, isProfile = false }) => {
    const dispatch = useDispatch();
    const navigate = useNavigate();
    const [isImage, setIsImage] = useState(false);
    const [image, setImage] = useState(null);
    const [post, setPost] = useState("");
    const { palette } = useTheme();
    const { _id } = useSelector((state) => state.user);
    const token = useSelector((state) => state.token);
    const mediumMain = palette.neutral.mediumMain;
    const medium = palette.neutral.medium;

    const handlePost = async () => {
        const formData = new FormData();
        formData.append('userId', _id);
        formData.append('description', post);
        if(image) {
            formData.append('picture', image);
            formData.append('picturePath', image.name);
        }

        const response = await fetch(`https://devconnect-api-5vst.onrender.com/posts`, {
            method: 'POST',
            headers: { Authorization: `Bearer ${token}`},
            body: formData,
        });

        const posts = await response.json();
        dispatch(setPosts({ posts }));
        setImage(null);
        setPost('');
        if(isProfile){
            navigate(0);
        }
    };

    return (
        <WidgetWrapper>
            <FlexBetween gap='1.5rem'>
                <UserImage image={picturePath} />
                <InputBase value={post} placeholder="What's your mind..." onChange={(e) => setPost(e.target.value)} sx={{ width: '100%', backgroundColor: palette.neutral.light, borderRadius: '2rem', padding: '1rem 2rem' }} />
            </FlexBetween>
            {isImage && (
                <Box border={`1px solid ${medium}`} borderRadius='5px' mt='1rem' p='1rem' >
                    <Dropzone acceptedFiles='.jpeg, .jpeg, .png' multiple={false} onDrop={(acceptedFiles) => setImage(acceptedFiles[0])}>
                        {({ getRootProps, getInputProps }) => (
                            <FlexBetween>
                                <Box {...getRootProps()} border={`2px dashed ${palette.primary.main}`} p='1rem' width='100%' sx={{ '&:hover': { cursor: 'pointer' }}}>
                                    <input {...getInputProps()} />
                                    {!image ? (
                                        <p>Add Image Here</p>
                                    ) : (
                                        <FlexBetween>
                                            <Typography>{image.name}</Typography>
                                            <EditOutlined />
                                        </FlexBetween>
                                    )}
                                </Box>
                                {image && (
                                    <IconButton onClick={() => setImage(null)} sx={{ width: '15%'}} >
                                        <DeleteOutlined />
                                    </IconButton>
                                )}
                            </FlexBetween>
                        )}
                    </Dropzone>
                </Box>
            )}
            <Divider sx={{ margin: '1.25rem 0'}} />
            <FlexBetween>
                <FlexBetween gap='0.25rem' onClick={() => setIsImage(!isImage)} >
                    <ImageOutlined sx={{ color: mediumMain }} />
                    <Typography color={mediumMain} sx={{ "&:hover": { cursor: 'pointer', color: medium }}} >Image</Typography>
                </FlexBetween>
                <Button disabled={!post} onClick={handlePost} style={{ color: palette.primary.light, backgroundColor: palette.primary.dark, borderRadius: '3rem' }} >Post</Button>
            </FlexBetween>
        </WidgetWrapper>
    )
};

export default MyPostWidget;