import { Box, Typography, useTheme } from "@mui/material";
import Friend from "../../components/friend";
import WidgetWrapper from "../../components/widgetWrapper";
import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { setFriends } from "../../state";

const FriendListWidget = ({ userId }) => {
    const dispatch = useDispatch();
    const token = useSelector((state) => state.token);
    const friends = useSelector((state) => state.user.friends);
    const { palette } = useTheme();

    const getFriends = async () => {
        const response = await fetch(`https://devconnect-api-5vst.onrender.com/users/${userId}/friends`, {
            method: 'GET',
            headers: { Authorization: `Bearer ${token}`}
        });
        const data = await response.json();
        dispatch(setFriends({ friends: data }));
    };

    useEffect(() => {
        getFriends();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    return (
        <WidgetWrapper>
            <Typography color={palette.neutral.dark} variant="h5" fontWeight='500' sx={{ mb: '1.5rem' }} >Friend List</Typography>
            <Box display='flex' flexDirection='column' gap='1.5rem'>
                { friends.length === 0 ? (
                    <Typography color={palette.neutral.medium} textAlign='center' mb='1rem' >No Friends</Typography>
                ) : 
                    friends.map((friend, index) => (
                        <Friend key={`${friend._id}-${index}`} friendId={friend._id} name={`${friend.firstName} ${friend.lastName}`} subtitle={friend.occupation} userPicturePath={friend.picturePath} />
                    ))
                }
            </Box>
        </WidgetWrapper>
    )
}

export default FriendListWidget;