import { useState } from "react";
import { Box, IconButton, InputBase, Typography, Select, MenuItem, FormControl, useTheme, useMediaQuery } from "@mui/material";
import { Search, DarkMode, LightMode, Help, Menu, Close } from '@mui/icons-material';
import { useDispatch, useSelector } from "react-redux";
import { setMode, setLogout, setSearchedUser } from "../../state";
import { useNavigate } from "react-router-dom";
import FlexBetween from "../../components/FlexBetween";

const Navbar = () => {
    const [isMobileMenuToggled, setIsMobileMenuToggled] = useState(false);
    const [search, setSearch] = useState('');
    const dispatch = useDispatch();
    const navigate = useNavigate();
    const user = useSelector((state) => state.user);
    const token = useSelector((state) => state.token);
    const isNonMobileScreens = useMediaQuery('(min-width: 1000px)');

    const theme = useTheme();
    const neutralLight = theme.palette.neutral.light;
    const dark = theme.palette.neutral.dark;
    const background = theme.palette.background.default;
    const primaryLight = theme.palette.primary.light;
    const alt = theme.palette.background.alt;

    const fullName = `${user.firstName} ${user.lastName}`;

    const handleSubmit = async () => {
        if(search === '' || !search){
            return;
        }

        const response = await fetch(`https://devconnect-api-5vst.onrender.com/users/${search}/search`, {
            method: "GET",
            headers: { Authorization: `Bearer ${token}`},
        });
        const searchedUser = await response.json();
        dispatch(setSearchedUser(searchedUser));
        setSearch('');
        navigate('/users/search');
    }

    return (
        <FlexBetween padding='1rem 6%' backgroundColor={alt} position='sticky' left='0' top='0' zIndex='12' >
            <FlexBetween gap='1.75rem'>
                <Typography fontWeight='bold' fontSize='clamp(1rem, 1.5rem, 5rem)' color='primary' onClick={() => navigate('/home')} sx={{ '&:hover': { color: primaryLight, cursor: 'pointer' }, }} >
                    DevConnect
                </Typography>
                {isNonMobileScreens && (
                    <FlexBetween backgroundColor={neutralLight} borderRadius='9px' gap='3rem' padding='0.1rem 1.5rem'>
                        <InputBase placeholder="Search for User..." value={search} name='search-bar' onChange={(e) => setSearch(e.target.value)} />
                        <IconButton onClick={handleSubmit} disabled={!search} >
                            <Search  sx={{ color: theme.palette.primary.dark }} />
                        </IconButton>
                    </FlexBetween>
                )}
            </FlexBetween>
            {isNonMobileScreens ? (
                <FlexBetween gap='2rem'>
                    <IconButton onClick={() => dispatch(setMode())}>
                        {theme.palette.mode === 'dark' ? (
                            <DarkMode sx={{ fontSize: '25px' }} />
                        ) : (
                            <LightMode sx={{ color: dark, fontSize: '25px' }} />
                        )}
                    </IconButton>
                    <Help sx={{ fontSize: '25px' }} />
                    <FormControl variant="standard" value={fullName}>
                        <Select value={fullName} sx={{ backgroundColor: neutralLight, width: '200px', borderRadius: '0.25rem', padding: '0.25rem 1rem', "& .MuiSvgIcon-root": { pr: '0.25rem', width: '3rem'}, "* .MuiSelect-select:focus": { backgroundColor: neutralLight }  }} input={<InputBase />} >
                            <MenuItem value={fullName}>
                                <Typography>{fullName}</Typography>
                            </MenuItem>
                            <MenuItem onClick={() => dispatch(setLogout())}>Log Out</MenuItem>
                        </Select>
                    </FormControl>
                </FlexBetween>
            ) : (
                <IconButton onClick={() => setIsMobileMenuToggled(!isMobileMenuToggled)}>
                    <Menu />
                </IconButton>
            )}

            {!isNonMobileScreens && isMobileMenuToggled && (
                <Box position='fixed' right='0' bottom='0' height='100%' zIndex='10' maxWidth='80vw' width='60%' minWidth='250px' backgroundColor={background}>
                    <Box display='flex' justifyContent='flex-end' p='1rem'>
                        <IconButton onClick={() => setIsMobileMenuToggled(!isMobileMenuToggled)}>
                            <Close />
                        </IconButton>
                    </Box>
                    <FlexBetween display='flex' flexDirection='column' justifyContent='center' alignItems='center' gap='3rem'>
                        <FlexBetween backgroundColor={neutralLight} borderRadius='9px' gap='3rem' padding='0.1rem 1.5rem' width='90%' >
                            <InputBase placeholder="Search for User..." value={search} name='search-bar' onChange={(e) => setSearch(e.target.value)} />
                            <IconButton onClick={handleSubmit} disabled={!search}>  
                                <Search sx={{ color: theme.palette.primary.dark }} />
                            </IconButton>
                        </FlexBetween>
                        <IconButton onClick={() => dispatch(setMode())} sx={{ fontSize: '25px' }} >
                            {theme.palette.mode === 'dark' ? (
                                <DarkMode sx={{ fontSize: '25px' }} />
                            ) : (
                                <LightMode sx={{ color: dark, fontSize: '25px' }} />
                            )}
                        </IconButton>
                        <Help sx={{ fontSize: '25px' }} />
                        <FormControl variant="standard" value={fullName}>
                            <Select value={fullName} sx={{ backgroundColor: neutralLight, width: '150px', borderRadius: '0.25rem', padding: '0.25rem 1rem', "& .MuiSvgIcon-root": { pr: '0.25rem', width: '3rem'}, "* .MuiSelect-select:focus": { backgroundColor: neutralLight }  }} input={<InputBase />} >
                                <MenuItem value={fullName}>
                                    <Typography>{fullName}</Typography>
                                </MenuItem>
                                <MenuItem onClick={() => dispatch(setLogout())}>Log Out</MenuItem>
                            </Select>
                        </FormControl>
                    </FlexBetween>
                </Box>
            )}
        </FlexBetween>
    )
};

export default Navbar;