import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { setPosts } from "../../state";
import PostWidget from "./postWidget";

const PostsWidget = ({ userId, isProfile = false}) => {
    const dispatch = useDispatch();
    let posts = useSelector((state) => state.posts);
    const token = useSelector((state) => state.token);
    const [ needUpdate, setNeedUpdate ] = useState('false')

    const getPosts = async () => {
        const response = await fetch('https://devconnect-api-5vst.onrender.com/posts', {
            method: "GET",
            headers: { Authorization: `Bearer ${token}`},
        });
        const data = await response.json();
        dispatch(setPosts({ posts: data }));
    };

    const getUserPosts = async () => {
        const response = await fetch(`https://devconnect-api-5vst.onrender.com/posts/${userId}/posts`, {
            method: "GET",
            headers: { Authorization: `Bearer ${token}`},
        });
        const data = await response.json();
        dispatch(setPosts({ posts: data }));
    };

    useEffect(() => {
        if(isProfile) {
            getUserPosts();
        } else {
            getPosts();
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [needUpdate]);

    return (
        <>
            {posts.map(
                ({
                    _id,
                    userId,
                    firstName,
                    lastName,
                    description,
                    location,
                    picturePath,
                    userPicturePath,
                    likes,
                    comments,
                }) => (
                    <PostWidget key={_id}  postId={_id} postUserId={userId} name={`${firstName} ${lastName}`} description={description} location={location} picturePath={picturePath} userPicturePath={userPicturePath} likes={likes} comments={comments} update={x => setNeedUpdate(x)} />
                )
            )}
        </>
    )
};

export default PostsWidget;