import { Box, useMediaQuery } from "@mui/material";
import { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import Navbar from "../navbar";
import FriendListWidget from "../widgets/friendListWidget";
import MyPostWidget from "../widgets/myPostWidget";
import PostsWidget from "../widgets/postsWidget";
import UserWidget from "../widgets/userWidget";
import { useParams } from "react-router-dom";

const ProfilePage = () => {
    const [user, setUser] = useState(null);
    const { userId } = useParams();
    const token = useSelector((state) => state.token);
    const currentUser = useSelector((state) => state.user._id);
    const isNonMobileScreens = useMediaQuery('(min-width: 1000px)');

    const getUser = async () => {
        const response = await fetch(`https://devconnect-api-5vst.onrender.com/users/${userId}`, {
            method: 'GET',
            headers: { Authorization: `Bearer ${token}` }
        });
        const data = await response.json();
        setUser(data);
    };

    useEffect(() => {
        getUser();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    if(!user) return null;

    return (
        <Box>
            <Navbar />
            <Box width='100%' padding='2rem 6%' display={isNonMobileScreens ? 'flex' : 'block'} gap='2rem' justifyContent='center' >
                <Box flexBasis={isNonMobileScreens ? '26%' : undefined }>
                    <UserWidget userId={userId} picturePath={user.picturePath} />
                    <Box m='2rem 0' />
                    <FriendListWidget userId={userId} />
                </Box>
                <Box flexBasis={isNonMobileScreens ? '42%' : undefined } mt={isNonMobileScreens ? undefined : '2rem'} >
                    { currentUser === userId && (
                        <MyPostWidget picturePath={user.picturePath} isProfile/>
                    )}
                    <PostsWidget userId={userId} isProfile />
                </Box>
            </Box>
        </Box>
    )
};

export default ProfilePage;